@tailwind base;
@tailwind components;
@tailwind utilities;

@layer utilities {
  @variants responsive {
    .circleSmall {
      width: 28px;
      height: 28px;
      border-radius: 50%;
    }
    .circle {
      width: 60px;
      height: 60px;
      border-radius: 50%;
    }
  }
}