.yellow {
  background-color: #CDB900;
  color: black;
}
.blue {
  background-color: #009CCD;
  color: black;
}
.dark {
  background-color: #4B394E;
  color: white;
}
.white {
  background-color: white;
  color: black;
}
.tuffyBold {
  font-family: 'tuffybold', sans-serif;
}
.tuffyItalic {
  font-family: 'tuffyitalic', sans-serif;
}
.tuffyRegular {
  font-family: 'tuffyregular', sans-serif;
}
.card {
  border: solid black 12px;
  border-radius: 50%;
  display: inline-block;
  margin: 0 auto;
  width: 70vw;
  height: 70vw;
  max-height: 75vh;
  max-width: 75vh;
}
.external {
  height: 1em;
  width: 1em;
  display: inline;
}
.skip-link {
  position: absolute;
  top: -40px;
  left: 0;
  background: #000000;
  color: white;
  padding: 8px;
  z-index: 100;
}
.skip-link:focus {
  top: 0;
}